.body{
  width: 300px;
  height: 405px;
  border: 0.5px solid;
  padding: 10px;
  color: black;
  background-color: rgba(8, 106, 5, 0.661);

  margin: auto;
  position: absolute;
  top: -10px; 
  bottom: 0;
  left: 0;
  right: 0;
}

.body::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 55px; 
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 1; 
}

.input_field{
  color: black;
  text-align: right;
}

.currentnumber{
  font-size: x-large;
}

.button {
  position: absolute;
  top: 55px; 
  left: 0%;
  width: 100%;  
}

.button_container {
  display: grid;
  grid-template-columns: repeat(3, 1fr); 
  gap: 0px; 
  height: 370px;
}